@import 'variables';

.button{
    width: 50px;
    height: 50px;
    padding: 10px;
    border-radius: 100px;
    gap: 10px;
    background: #FFFFFF;
    box-shadow: 0px 2px 5px 0px #26334D08;
    border: none;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;

    &_active{
        position: absolute;
        right: -10%;
        top: -20%;
    }

    &_little{
        width: 40px;
        height: 40px;
        padding: 6px;
        border-radius: 100px;
        background-color: #FFFFFF;
        border: 2px solid #F5F6F7;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    &_search{
        width: 43px; 
        height: 40px;
        background: #FFFFFF;

        &__right{
            width: 43px; 
            height: 40px;
            background: #FFFFFF;
            padding: 6px 10px 6px 5px;
            border-radius: 0px 100px 100px 0px;
            border: 2px solid #F5F6F7;
            border-left: none;
            display: flex;
            justify-content: center;
            align-items: center;

        }
       

        &__left{
            width: 43px; 
            height: 40px;
            background: #FFFFFF;
            padding: 6px 5px 6px 10px;
            border-radius: 100px 0px 0px 100px;
            border: 2px solid #F5F6F7;
            display: flex;
            justify-content: center;
            align-items: center;


        }
    }
  }


.orange  g  rect{
    fill: $notifications-orange;

}

  